.header{
    display: flex;
    
    justify-content: space-between;
    flex:1;
    padding:12px;
    position: relative;
    align-items: center;
    height: 40px;
}

.Menu{
    display:flex;
   
    flex: 1;
    justify-content: flex-end;
}

.menu-icon{

    font-size: 24px;
}

.mobile-menu{

    display: none;
}

.web-menu{
    display:flex;
    
}

.logo{

    display:flex;
    font-size: 20px;
    color: #fefefe;
    font-family: 'Playfair Display', serif;
    font-style: bold;

    
    

}

@media only screen and (max-width: 720px ){
    .mobile-menu{
        display: flex;
    }
    .web-menu{
        display: none;
    }
    .logo{
        font-size: 19px;
    }



}